html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #000000;
  font-size: 2rem;
}
body.overflow-hidden {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4.6rem;
}

h3 {
  font-size: 6rem;
  text-align: center;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

body > section {
  padding: 10rem 0;
}

.header {
  background-color: #4CB8B3;
}
@media (min-width: 768px) {
  .header.fixed {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .contenido-header h1 {
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-size: 3rem;
  font-weight: 900;
  color: #FFFFFF;
  margin-top: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin: 0;
    text-align: left;
  }
}

.navbar-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navbar-principal {
    flex-direction: row;
  }
}
.navbar-principal a {
  color: #FFFFFF;
  transition-property: color;
  transition: 0.3s ease-in-out;
  font-family: "Bona Nova", sans-serif;
  font-weight: 900;
  font-size: 2.5rem;
}
.navbar-principal a:hover, .navbar-principal a.active {
  color: #fdda00;
}

.video {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  display: flex;
  align-items: center;
  position: absolute;
  background-color: black;
  background: linear-gradient(270deg, rgba(111, 27, 153, 0.68), rgba(48, 12, 66, 0.9));
  inset: 0;
}
.video video {
  display: block;
  width: auto;
}
@media (min-width: 1200px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
  margin: 0 auto;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-style: italic;
  font-size: 5rem;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 5.5rem;
    text-align: left;
  }
}
.contenido-video p {
  display: inline-block;
  font-style: italic;
  font-size: 2.6rem;
  font-weight: 700;
  margin-top: 2rem;
  padding: 1rem 2rem;
  color: #FFFFFF;
  background-color: #F53756;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-video p {
    text-align: left;
    margin-top: 1rem;
  }
}

.sobre-festival {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem 5rem;
  }
}
@media (min-width: 768px) {
  .sobre-festival .info-festival {
    grid-column: 1/3;
  }
}
@media (min-width: 1200px) {
  .sobre-festival .info-festival {
    grid-column: 2/3;
    margin-top: -26rem;
  }
}
.sobre-festival .info-festival p {
  margin: 0.5rem;
}

.contenido-festival {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
  text-align: center;
  font-size: 5rem;
  font-family: "Bona Nova", sans-serif;
  font-weight: 900;
}
@media (min-width: 768px) {
  .contenido-festival h2 {
    padding: 2rem;
  }
}
@media (min-width: 1200px) {
  .contenido-festival h2 {
    padding: 0;
    text-align: left;
  }
}
.contenido-festival .fecha {
  color: #4CB8B3;
  font-weight: 700;
  text-align: center;
  font-size: 3rem;
  font-family: "Bona Nova", sans-serif;
  font-weight: 900;
}
@media (min-width: 1200px) {
  .contenido-festival .fecha {
    text-align: left;
  }
}

.lineup {
  background-color: #F53756;
}
.lineup h3, .lineup .dia {
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-weight: 900;
  font-size: 7rem;
  text-align: center;
  color: #FFFFFF;
}
.lineup .dia {
  font-size: 3rem;
  text-transform: uppercase;
}

.escenarios-contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.escenario {
  margin-top: 2rem;
}
.escenario .nombre-escenario {
  text-align: center;
  font-size: 3rem;
  font-style: italic;
  font-weight: 900;
  text-transform: uppercase;
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-size: 2.5rem;
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-weight: 900;
}
.calendario li span {
  font-size: 2rem;
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-weight: 700;
}

.bg-amarillo {
  background-color: #fdda00;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #ffe331;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde li:nth-child(odd) {
  background-color: #71c6c3;
}

.galeria {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria-imagenes img {
  height: 20rem;
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 1200px) {
  .galeria-imagenes img:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
}

.modal {
  display: grid;
  place-items: center;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  animation: fadeIn ease-in-out 0.5s;
}
.modal.fade-out {
  animation: fadeOut ease-in-out 0.5s;
}
.modal .contenedor-imagen {
  position: relative;
  max-width: 100rem;
  border-radius: 0 1.2rem 0 0;
}
.modal .contenedor-imagen .btn-cerrar {
  position: absolute;
  background-color: #6f1b99;
  width: 4rem;
  height: 4rem;
  font-weight: 700;
  color: gold;
  top: 0;
  right: 0;
  font-size: 2.6rem;
  border-radius: 1rem 0 1rem 1rem;
  border: none;
  cursor: pointer;
  transition-property: scale background-color;
  transition: 0.2s ease-in;
}
.modal .contenedor-imagen .btn-cerrar:hover {
  width: 5.2rem;
  height: 5.2rem;
  background-color: #300c42;
  color: #998100;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.contenedor-boletos {
  background-color: #4CB8B3;
}
.contenedor-boletos h3 {
  color: #FFFFFF;
}
.contenedor-boletos .boletos {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-boletos .boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 1200px) {
  .pase:hover {
    transform: scale(1.1);
  }
}
.pase-nombre {
  color: #FFFFFF;
  font-size: 3.5rem;
  font-family: "Bona Nova", sans-serif;
  font-weight: 900;
  text-align: center;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.pase-header {
  background-color: #F53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 2.2rem;
  padding: 1rem;
}
.pase-body {
  background-color: #FFFFFF;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li {
  padding: 2rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #FFFFFF;
  padding: 3rem;
}
.pase-footer p {
  font-size: 2.8rem;
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-weight: 900;
  background-color: #6f1b99;
  padding: 1rem 0;
  border-radius: 1rem;
  transition: background-color 0.3s ease-in-out;
}
.pase-footer p:hover {
  background-color: #300c42;
  cursor: pointer;
}

.footer {
  background-color: #F53756;
  padding: 3rem 1rem;
}
.footer p {
  font-family: "Bona Nova", sans-serif;
  font-style: italic;
  font-size: 3rem;
  font-weight: 900;
  margin: 0;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 1200px) {
  .footer p {
    font-size: 3.4rem;
  }
}